.message {
    display: flex;
    align-items: flex-end;
    margin: 10px;
}
.avatar {
    height: 50px;
    width: 50px;
}
.content {
    padding: 10px;
    background-color: rgba(255, 255, 255, 0.3);
}
.angle {
    width: 15px;
    height: 15px;
    background: radial-gradient(circle at 0 0, rgba(0, 0, 0, 0) 15px, rgba(255, 255, 255, 0.3) 0);
}
.text {
    max-width: 300px;
    word-wrap: break-word;
}
.name {
    font-size: 20px;
    color: blue;
}
.time {
    display: flex;
    justify-content: flex-end;
}

